<template>
    <div class="board h-100 d-flex flex-column justify-content-around" ref="board">
        <div class="board-side" ref="board_side_1">
            <div v-for="board_row in 2" :key="board_row" class="h-50 row board-row d-flex align-items-center">
                <div v-for="row_hole in 4" :key="row_hole" class="p-1" :style="{'width': hole_size, 'height': hole_size}">
                    <div class="hole h-100 w-100" :class="{'can_hover_opp': can_hover_opp && turn == 'opp', 'focused_hole': focused_hole_moving == 'opp'+((board_row - 1) * 4 + row_hole)}" @click="click_hole('opp', (board_row - 1) * 4 + row_hole)">
                        {{ pebbles["opp"][(board_row - 1) * 4 + row_hole] }}
                    </div>
                </div>
            </div>
        </div>

        <div class="board-side">
            <div v-for="board_row in 2" :key="board_row" class="h-50 row board-row d-flex align-items-center">
                <div v-for="row_hole in 4" :key="row_hole" class="p-1" :style="{'width': hole_size, 'height': hole_size}">
                    <div class="hole h-100 w-100" :class="{'can_hover_me': can_hover_me && turn == 'me', 'focused_hole': focused_hole_moving == 'me'+((board_row - 1) * 4 + row_hole)}" @click="click_hole('me', (board_row - 1) * 4 + row_hole)">
                        {{ pebbles["me"][(board_row - 1) * 4 + row_hole] }}
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>

export default {
    props: {
        who_turn: {
            type: String,
            default: 'me'
        }
    },
    data () {
        return {
            hole_size: '0px',
            pebbles: {
                "opp": {},
                "me": {}
            },
            turn: this.who_turn,
            can_hover_opp: true,
            can_hover_me: true,
            direction: 'clock',
            can_I_click: true,
            can_opp_click: true,
            focused_hole_moving: null,
            timer: null,
            SPEED_TIMER: 300, // milliseconds
        }
    },
    methods: {
        update_hole_size() {
            let length = this.$refs.board_side_1.clientHeight
            let width = this.$refs.board_side_1.clientWidth
            let size = 0
            if (length < width) {
                size = length / 2
            }
            else {
                size = width / 2
            }
            this.hole_size = size + 'px'
        },
        move_pebbles(who, num) {
            this.make_turn('none')
            this.focused_hole_moving = who + num
            var in_hand = 0
            var starting = true
            var has_got_from_other = false
            this.timer = setInterval(function() {
                if (!starting && in_hand <= 0 && this.pebbles[who][num] == 1) {
                    clearInterval(this.timer)
                    this.focused_hole_moving = null
                    this.make_turn('switch')
                    return false
                }
                else if (in_hand <= 0 && this.pebbles[who][num] > 1) {
                    var from_other = 0
                    var other_id = ''
                    var num_other = null
                    if (who == 'me' && num >= 1 && num <= 4) {
                        other_id = 'opp'
                        num_other = num + 4
                        from_other = this.pebbles[other_id][num_other]
                        if (from_other <= 0) {
                            num_other = num
                            from_other = this.pebbles[other_id][num_other]
                        }
                    }
                    else if (who == 'opp' && num >= 5 && num <= 8) {
                        other_id = 'me'
                        num_other = num - 4
                        from_other = this.pebbles[other_id][num_other]
                        if (from_other <= 0) {
                            num_other = num
                            from_other = this.pebbles[other_id][num_other]
                        }
                    }

                    if (!starting && !has_got_from_other && from_other > 0) {
                        this.pebbles[who][num] += from_other
                        this.pebbles[other_id][num_other] = 0
                        has_got_from_other = true
                        this.emit_pts()
                        return false
                    }

                    in_hand = this.pebbles[who][num]
                    this.pebbles[who][num] = 0
                }
                else if (starting && this.pebbles[who][num] == 1) {
                    in_hand = this.pebbles[who][num]
                    this.pebbles[who][num] = 0
                }

                starting = false
                has_got_from_other = false

                if (who == "me" && this.direction == 'clock' || who == 'opp' && this.direction == 'c-clock') {
                    if (num == 4) {
                        num = 8
                    }
                    else if (num == 5) {
                        num = 1
                    }
                    else if (num > 4) {
                        num -= 1
                    }
                    else {
                        num += 1
                    }
                }
                else {
                    if (num == 8) {
                        num = 4
                    }
                    else if (num == 1) {
                        num = 5
                    }
                    else if (num > 4) {
                        num += 1
                    }
                    else {
                        num -= 1
                    }
                }

                this.focused_hole_moving = who + num

                this.pebbles[who][num] += 1
                in_hand -= 1
            }.bind(this), this.SPEED_TIMER)
        },
        click_hole(who, num) {
            if (who == 'me' && this.can_I_click || who == 'opp' && this.can_opp_click) {
                if (this.pebbles[who][num] > 0) {
                    this.move_pebbles(who, num)
                }
            }
        },
        make_turn(who) {
            if (who == 'switch') {
                if (this.turn == 'me') {
                    this.turn = 'opp'
                }
                else {
                    this.turn = 'me'
                }
                who = this.turn
            }
            if (who == 'me') {
                this.can_hover_me = true
                this.can_hover_opp = false
                this.can_I_click = true
                this.can_opp_click = false
            }
            else if (who == 'opp') {
                this.can_hover_me = false
                this.can_hover_opp = true
                this.can_I_click = false
                this.can_opp_click = true
            }
            else if (who == 'none') {
                this.can_hover_me = false
                this.can_hover_opp = false
                this.can_I_click = false
                this.can_opp_click = false
            }

            if (who == 'me' || who == 'opp') {
                this.$emit("turn", who)
            }
        },
        emit_pts() {
            let opp_pts = 0
            let me_pts = 0
            for (let i = 1; i <= 8; i++) {
                opp_pts += this.pebbles["opp"][i]
                me_pts += this.pebbles["me"][i]
            }
            this.$emit("pts", {
                "opp": opp_pts,
                "me": me_pts
            })
        }
    },
    created() {
        for (let i = 1; i <= 8; i++) {
            this.pebbles["opp"][i] = 2
            this.pebbles["me"][i] = 2
        }
        this.emit_pts()

        this.make_turn(this.turn)
    },
    mounted() {
        this.update_hole_size()
        window.addEventListener("resize", this.update_hole_size)
        this.$emit("turn", this.turn)
    },
    beforeUnmount() {
        window.removeEventListener("resize", this.update_hole_size);
        if (!this.timer) {
            clearInterval(this.timer)
        }
    }
}
</script>

<style scoped>

.board-side {
    height: 47%;
    border : 2px solid black;
    border-radius: 30px;
}
.hole {
    border-radius: 100%;
    border: 2px solid black;
    display: flex;
    justify-content: center;
    align-items: center;
}

@media (hover: hover) {
    .hole.can_hover_me:hover, .hole.can_hover_opp:hover {
        background-color: black;
        color: white;
        cursor: pointer;
    }
}

.hole.focused_hole {
    background-color: black;
    color: white;
}

.board-row {
    display: flex;
    justify-content: center;
}
</style>