<template>
    <div class="h-100 d-flex flex-column justify-content-between">
        <div class="dashboard-player">
            <DashBoardPlayer 
                :is_clockwise="p_start == 'opp' && p_start_c == 'clock' || p_start == 'me' && p_start_c == 'c-clock'" 
                :player_name="'Sasuke'"
                :is_turn="who_turn == 'opp'"
                :pts="opp_pts"
                />
        </div>

        <div class="board-container">
            <div class="row h-100">
                <div class="col h-100">
                    <Board @turn="update_turn" @pts="update_pts" />
                </div>
            </div>
        </div>

        <div class="dashboard-player">
            <DashBoardPlayer 
                :is_clockwise="p_start == 'me' && p_start_c == 'clock' || p_start == 'opp' && p_start_c == 'c-clock'" 
                :player_name="'Naruto'"
                :is_turn="who_turn == 'me'"
                :pts="me_pts"
                />
        </div>
    </div>
</template>

<script>
// import axios from 'axios'
import Board from '@/components/game/Board.vue'
import DashBoardPlayer from '@/components/game/DashBoardPlayer.vue'

export default {
    components: {
        Board,
        DashBoardPlayer
    },
    props: {
        p_start: {
            type: String,
            default: "me"
        },
        p_start_c: {
            type: String,
            default: "clock"
        },
    },
    data () {
        return {
            who_turn: this.p_start,
            opp_pts: 0,
            me_pts: 0
        }
    },
    methods: {
        update_turn(who) {
            this.who_turn = who
        },
        update_pts(pts) {
            this.opp_pts = pts.opp
            this.me_pts = pts.me
        }
    },
}
</script>

<style scoped>
.board-container {
    height: 74%;
}

.dashboard-player {
    height: 12%;
}
</style>